<template>
  <div class="product">
    <Navbar />
    <div class="container mt-3">
      <div class="product-links">
        <p>
          <router-link to="/catalog">
            Каталог товаров
          </router-link>&nbsp;>
          <a href="#">{{ product.category }}</a>
          >
          {{ product.name }}
        </p>
      </div>
      <div class="product-title text-center">
        <h4>{{ product.name }}</h4>
      </div>
      <div class="row mb-3">
        <div class="col-xl-7 col-lg-6 col-md-12 col-sm-12 col-12 mb-5 mt-4 mr-3">
          <!-- слайдер -->
          <hooper :infinite-scroll="true">
            <slide
              v-for="slids in slid"
              :key="slids.id"
            >
              <b-img
                fluid
                :src="`http://bohmann.shop${slids}`"
              />
            </slide>
            <hooper-navigation slot="hooper-addons" />
          </hooper>
        </div>
        <div
          class="col-xl-4 col-lg-5 col-md-12 col-sm-12 col-12 mr-3 d-flex justify-content-center align-items-center mt-3 mb-3"
        >
          <div class="addcart border border-primary">
            <div class="value p-3 text-center">
              {{ product.price }} грн
            </div>
            <div class="account p-2">
              <div class="add pt-2 pb-2">
                Добавить в:
              </div>
              <div class="nacelle d-flex align-items-center justify-content-start">
                <img
                  class="img-account"
                  src="@/img/account-img/account-cart.svg"
                  alt="cart"
                >
                <h4 class="account pl-1">
                  <a
                    href="#"
                    @click="addToCart(product)"
                  >В корзину</a>
                </h4>
              </div>
              <div class="desires d-flex align-items-center justify-content-start">
                <img
                  class="img-account"
                  src="@/img/account-img/account-favorite.svg"
                  alt="FAVOURITE"
                >
                <h4 class="account pl-1">
                  <a href="#">В список желаний</a>
                </h4>
              </div>
            </div>
          </div>
        </div>
        <div class="col-xl-12 col-lg-12 col-md-12 col-sm-12 col-12">
          <div class="row border-top border-bottom">
            <div class="col-xl-6 border-right">
              <div class="product-subtitle mt-2 mb-3 text-center">
                Характеристика
              </div>
              <div>
                <p>
                  <strong>{{ product.name }}</strong>
                  {{ product.description }}
                </p>
              </div>
            </div>
            <div class="col-xl-6 p-0">
              <div class="product-subtitle ml-4 mt-2 mb-2 text-center">
                Отзывы
              </div>
              <div class="border-top pl-0 mb-2" />
              <div
                v-for="com in comment"
                :key="com.id"
                class="pl-2 media border-bottom"
              >
                <div class="media-body">
                  <h5 class="mt-0 mb-0">
                    {{ com.username }}
                  </h5>
                  <star-rating
                    :star-size="20"
                    :rating="com.rating"
                    :read-only="true"
                    :show-rating="false"
                    :glow="10"
                    :rounded-corners="true"
                    :star-points="[
                      23,
                      2,
                      14,
                      17,
                      0,
                      19,
                      10,
                      34,
                      7,
                      50,
                      23,
                      43,
                      38,
                      50,
                      36,
                      34,
                      46,
                      19,
                      31,
                      17
                    ]"
                  />
                  <p>{{ com.text }}</p>
                </div>
              </div>

              <div
                v-if="log === 'true'"
                class="form-group ml-2 mr-3"
              >
                <label for="exampleFormText">
                  <h4 class="article-title mb-0 mt-1">Написать отзыв:</h4>
                </label>
                <textarea
                  id="exampleFormText"
                  v-model="textComment"
                  class="form-control"
                  required
                  rows="3"
                />
                <star-rating
                  v-model="rait"
                  required
                  :star-size="30"
                  :show-rating="false"
                  :glow="10"
                  :rounded-corners="true"
                  :star-points="[
                    23,
                    2,
                    14,
                    17,
                    0,
                    19,
                    10,
                    34,
                    7,
                    50,
                    23,
                    43,
                    38,
                    50,
                    36,
                    34,
                    46,
                    19,
                    31,
                    17
                  ]"
                />
                <div class="text-right mt-2">
                  <button
                    class="btn btn-dark border help-b"
                    type="submit"
                    @click="addComment"
                  >
                    Оставить отзыв
                  </button>
                </div>
              </div>
            </div>
          </div>
        </div>
        <div class="col-xl-2 col-lg-2 d-none d-lg-block ml-3" />
      </div>
    </div>
    <section>
      <div class="d-flex flex-row justify-content-center">
        <div class="col-xl-12 col-lg-12 col-md-12 col-sm-12 col-12">
          <div class="container p-0">
            <div class="row">
              <div class="col-12 col-xl-12" />
            </div>
          </div>
        </div>
      </div>
    </section>
    <Footer />
  </div>
</template>

<script>
import { Hooper, Slide, Navigation as HooperNavigation } from "hooper";
import "hooper/dist/hooper.css";
import axios from "axios";
import Navbar from "./Navbar.vue";
import Footer from "./Footer.vue";
import StarRating from "vue-star-rating";
export default {
    name: "Prod",
    components: {
        Navbar,
        Footer,
        StarRating,
        Hooper,
        Slide,
        HooperNavigation
    },
    props: ["rating", "user"],
    data() {
        return {
            log: this.$store.state.login,
            rait: 0,
            textComment: "",
            Id: this.$route.params.Pid,
            product: {},
            comment: [],
            slid: []
        };
    },
    mounted() {
        axios
            .get(`http://bohmann.shop/api/products/${this.Id}/`)
            .then(response => (this.product = response.data));
        axios
            .get(`http://bohmann.shop/api/products/${this.Id}/`)
            .then(response => (this.comment = response.data.comments));
        axios
            .get(`http://bohmann.shop/api/products/${this.Id}/`)
            .then(response => (this.slid = response.data.images));
    },
    methods: {
        addComment(e) {
            e.preventDefault();
            axios
                .post("http://bohmann.shop/api/comments/", {
                    id: this.$cookies.get("user"),
                    username: this.user.name,
                    text: this.textComment,
                    rating: this.rait,
                    product: this.product.id
                })
                .then(response => {
                    window.location.reload();
                    console.log(response);
                })
                .catch(e => {
                    this.errors.push(e);
                });
        },
        addToCart(item) {
            console.log(this.product.id);
            this.$store.commit("addToCart", item);
            let found = this.$store.state.cart.find(
                product => product.id === item.id
            );
            found.inCart = true;
        }
    }
};
</script>

<style scoped>
.hhp {
  margin-top: 10px;
  height: 100px !important;
}

.img-fluid {
  width: 100%;
  height: 100%;
}
</style>
